.header {
  max-width: 880px;
  padding: 0 0 41px;
  margin: 45px auto 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 1023px) {
  .header {
    max-width: 80%;
  }
}

@media screen and (max-width: 767px) {
  .header {
    max-width: 100%;
  }
}
